<template>
  <transition name="slide">
    <div class="add-song" v-show="showflag" @click.stop>
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
        <div class="close" @click="hide">
          <i class="icon-close"></i>
        </div>
      </div>
      <div class="search-box-wrapper">
        <search-box placeholder='搜索歌曲' @query='onQueryChange'></search-box>
      </div>
      <div class="shortcut" v-show='!query'>
        <switches :switches='switches' :currentIndex='currentIndex' @switch='switchItem'></switches>
        <div class="list-wrapper">
          <scroll :refreshDelay='refreshDelay'  ref="songList" v-if="currentIndex===0" class="list-scroll" :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select='selectSong'>
              </song-list>
            </div>
          </scroll>
          <scroll :refreshDelay="refreshDelay" ref="searchList" v-if="currentIndex===1" class="list-scroll"
                  :data="searchHistory">
            <div class="list-inner">
              <searchHistoryList @delete="deleteSearchHistory" @select="addQuery" :searches="searchHistory"></searchHistoryList>
            </div>
          </scroll>
        </div>
      </div>
      <div class="search-result" v-show='query'>
        <search-list :query='query' :showSinger="showSinger" @select="selectSuggest" @listScroll="blurInput"></search-list>
      </div>
     <top-tip ref="toptip">
       <div class="tip-title">
         <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌曲已经添加到播放列表</span>
         </div> 
       </div>
     </top-tip>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">

  import searchBox from 'base/search-box/search-box'
  import searchList from 'components/search-list/search-list'
  import {searchMixin} from 'common/js/mixin'
  import switches from 'base/switches/switches'
  import Scroll from 'base/scroll/scroll'
  import {mapGetters,mapActions} from 'vuex'
  import songList from 'base/songlist/songlist'
  import Song from 'common/js/song'
  import searchHistoryList from 'components/searchHistoryList/searchHistoryList'
  import topTip from 'components/top-tip/top-tip'
  export default {
  mixins:[searchMixin],
   data(){
     return {
       showflag:false,
       showSinger:false,
       currentIndex:0,
       refreshDelay:100,
       switches:[
         {name:'最近播放'},
         {name:'搜索历史'}
       ]
     }
   },
   computed: {
      ...mapGetters([
        'playHistory',
        'searchHistory'
      ])
    },
   methods:{
     selectSong(song,index){
      if(index !== 0){
        this.insertSong(new Song (song))
        this.showTip()
      }
      
     },
     ...mapActions([
       'insertSong'
     ]),
     switchItem(index){
       this.currentIndex=index
     },
     onQueryChange(newquery){
      this.query=newquery
     },
     show(){
       this.showflag = true
       setTimeout(()=>{
         if(this.currentIndex === 0){
           this.$refs.songList.refresh()
         }else{
           this.$refs.searchList.refresh()
         }
       },20)
     },
     hide(){
       this.showflag = false
     },
     selectSuggest(){//保存搜索记录
        this.saveSearch()
        this.showTip()
     } ,
     showTip(){
       this.$refs.toptip.show()
     }
   },
   components:{
     searchBox,
     searchList,
     switches,
     Scroll,
     songList,
     searchHistoryList,
     topTip
   }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .add-song
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    z-index: 200
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .header
      position: relative
      height: 44px
      text-align: center
      .title
        line-height: 44px
        font-size: $font-size-large
        color: $color-text
      .close
        position: absolute
        top: 0
        right: 8px
        .icon-close
          display: block
          padding: 12px
          font-size: 20px
          color: $color-theme

    .search-box-wrapper
      margin: 20px
    .shortcut
      .list-wrapper
        position: absolute
        top: 165px
        bottom: 0
        width: 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 124px
      bottom: 0
      width: 100%
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
</style>


