import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import 'common/stylus/index.styl'
import VueLazyLoad from 'vue-lazyload'
import fastclick from 'fastclick'
import store from './store'
/* eslint-disable no-new */
fastclick.attach(document.body)/*阻止点击的300ms 的延迟 但它会与我们的scroll中的允许点击产生冲突这样会导致zu
*阻止点击 要想解决这一问题 将要点击的元素的class设置为needsclick 这样当我们的fastclick检测到时就会允许点击
*/
Vue.use(VueLazyLoad,{
  loading: require('common/image/logo.png')
})
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  store
})



