import Vue from 'vue'
import Router from 'vue-router'
//import Hello from '@/components/Hello'
// import Recommend from 'components/recommend/recommend'
// import Singer from 'components/singer/singer.vue'
// import Rank from 'components/rank/rank'
// import Search from 'components/search/search'
// import SingeraDtall from 'components/singer-detall/singer-detall'
// import Disc from 'components/disc/disc'
// import RankList from 'components/rank-list/rank-list'
// import userCenter from 'components/user-center/user-center'
Vue.use(Router)

const Recommend = (resolve) => {
  import('components/recommend/recommend').then((module) => {
    resolve(module)
  })
}

const Singer = (resolve) => {
  import('components/singer/singer').then((module) => {
    resolve(module)
  })
}

const Rank = (resolve) => {
  import('components/rank/rank').then((module) => {
    resolve(module)
  })
}

const RankList = (resolve) => {
  import('components/rank-list/rank-list').then((module) => {
    resolve(module)
  })
}

const Search = (resolve) => {
  import('components/search/search').then((module) => {
    resolve(module)
  })
}

const SingerDetall = (resolve) => {
  import('components/singer-detall/singer-detall').then((module) => {
    resolve(module)
  })
}

const Disc = (resolve) => {
  import('components/disc/disc').then((module) => {
    resolve(module)
  })
}


const UserCenter = (resolve) => {
  import('components/user-center/user-center').then((module) => {
    resolve(module)
  })
}


export default new Router({
  routes: [
  	{
  		path:'/',
  		redirect:'/recommend'
  	},
    {
      path: '/recommend',
      component: Recommend,
      children:[
        {
          path:':id',
          component:Disc
        }
      ]
    },
    {
    	path: '/singer',
    	component: Singer,
      children:[
        {
          path:':id',
          component: SingerDetall
        }
      ]
    },
    {
    	path: '/rank',
    	component:Rank,
      children:[
        {
          path:':id',
          component:RankList
        }
      ]
    },
    {
    	path: '/search',
    	component:Search,
      children:[
        {
          path:":id",
          component:SingerDetall
        }
      ]
    },
    {
      path:'/user',
      component:UserCenter
    }
  ]
})



