<template>
  <scroll class="suggest" 
          :data="result" 
          @scrollToEnd="searchMore"
          :puallup="puallup"
          :beforeScroll="beforeScroll"
          ref="suggest"
          @beforeScroll='listScroll'
          >
    <ul class="suggest-list">
      <li class="suggest-item" 
          v-for="item in result"
          @click = 'setSingerItem(item)'
          >
        <div class="icon">
          <i :class="getIconClass(item)"></i>
        </div>
        <div class="name">
          <p v-html="getItemText(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore"></loading>
    </ul>
  </scroll>
</template>
<script>
  import {search} from 'api/search'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  import Scroll from 'base/scroll/scroll'
  import loading from 'base/loading/loading'
  import {mapMutations,mapGetters,mapActions} from 'vuex'
  import Singer from 'common/js/singer'
  const perpage = 20
  const TYPE_SINGER = 'singer'
  export default{
    props:{
      showSinger: {
        type: Boolean,
        default: true
      },
      query: {
        type: String,
        default: ''
      }
    },
    data(){
      return{
        result:[],
        puallup:true,
        hasMore:true,
        beforeScroll:true
      }
    },
    methods:{
      _getSearch(){
        this.page = 1
        this.hasMore = true
        this.$refs.suggest.scrollTo(0, 0)
        search(this.query, this.page, this.showSinger, perpage).then((res) => {
          if (res.code === ERR_OK) {
            this.result = this._genResult(res.data)
            console.log(this.result)
            this. _checkMore(res.data)
          }
          
        })
       
      },
      searchMore(){//获取更多 搜索来的 歌单 是分页的 page 的加减 表示了 当前的获取歌单页
        if(!this.hasMore){//在scroll 组件中监听 scroll 组件中 设置了 当滑动结束并且滑动偏移多少事 触发这个 scroll
          //头end
          return
        }
        this.page++
        search(this.query, this.page, this.showSinger, perpage).then((res) => {
          if (res.code === ERR_OK) {
            this.result = this.result.concat(this._genResult(res.data))//数据合并
            console.log(this.result)
            
          }
      })
    },
    listScroll(){
      this.$emit('listscroll')
    },
    _checkMore(data) {//是否还有更多的歌曲
        const song = data.song
        if (!song.list.length || (song.curnum + song.curpage * perpage) > song.totalnum) {//如果当前的歌曲长度不存在 或者大于总长度 就没有更多
          this.hasMore = false
        }
      },
      _genResult(data) {//获取到的数据合并
        let ret = []
        if (data.zhida && data.zhida.singerid) {
          ret.push({...data.zhida, ...{type: TYPE_SINGER}})
        }
        if (data.song) {
          ret = ret.concat(this._normalizeSongs(data.song.list))
        }
        return ret
      }, 
       _normalizeSongs(list) {
        let ret = []
        list.forEach((musicData) => {
          if (musicData.songid && musicData.albummid) {
            ret.push(createSong(musicData))
          }
        })
        return ret
      } ,
      getIconClass(item){
        if(item.type == TYPE_SINGER){
          return 'icon-mine'
        } else {
          return 'icon-music'
        }
      },
      getItemText(item){
        if(item.type == TYPE_SINGER){
          return item.singername
        }else{
          return `${item.name}-${item.singer}`
        }
      },
      setSingerItem(item){
        console.log(item)
        if(item.type === TYPE_SINGER){
           const singer = new Singer({
            id:item.singermid,
            name:item.singername
          })
          console.log(singer,4)
          this.setSinger(singer)
          this.$router.push({
          path:`/search/${singer.id}`
        })
        }else{
          this.insertSong(item)
        }
        this.$emit('select',item)
      },
      ...mapMutations({
        setSinger:"SET_SINGER"
      }),
      ...mapGetters([
        'singer'
      ]),
      ...mapActions([
        'insertSong'
      ])
    },
    components:{
      Scroll,
      loading
    },
    watch:{
      query(query){
         this._getSearch() 
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 20PX 30px 0
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


