<template>
	<div ref="wrapper">
		<slot></slot>
	</div>
</template>
<script type="text/ecmascript-6">
	import BScroll from 'better-scroll'
	/*betterScroll组件封装滑动*/
	export default {
		created(){
			
			this.cnsole()
		},
		props:{
			probeTable:{
				type:Number,
				default:1
			},
			click:{
				type:Boolean,
				default:true
			},
			data:{
				type:Array,
				default: null
			},
			listenScroll:{/*是否监听滚动事件*/
				type:Boolean,
				default:false
			},
			probeType: {//当该值为3时 才能监听执行onscroll事件
        type: Number,
        default: 1
      },
			puallup:{
				type:Boolean,
				default:false
			},
			beforeScroll:{
				type:Boolean,
				default:true
			},
			refreshDelay:{
				type:Number,
				default: 20
			}
		},
		mounted(){
			setTimeout(()=>{
				this._initScrpll()
			},20)

		},
		methods:{
			cnsole(){
				console.log(this.data)
				
			},
			_initScrpll(){
				
				if(!this.$refs.wrapper){
					return
				}
				this.scroll = new	BScroll(this.$refs.wrapper,{
					probeType: this.probeType,
					click: this.click
				})
				if(this.listenScroll){
					let me = this 
					this.scroll.on('scroll',(pos)=>{//	probeType: {//当该值为3时 才能监听执行onscroll事件 pos传出
						//的是当前的scroll的x y 轴的值
						me.$emit('scroll',pos)
					})
				}	
				if(this.puallup){
					this.scroll.on('scrollEnd',()=>{
						 if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd')
            }
					})
				}
				if(this.beforeScroll){
					this.scroll.on('beforeScrollStart',()=>{
						this.$emit('beforeScroll')
					})
				}
			},
			enable(){
				this.scroll && this.scroll.enable()/*作用：启用 better-scroll, 默认 开启*/
			},
			disable(){
				this.scroll && this.scroll.disable()/*禁用 better-scroll，DOM 事件（如 touchstart、touchmove、touchend）的回调函数不再响应*/
			},
			refresh(){
				this.scroll && this.scroll.refresh()/*重新计算 better-scroll，当 DOM 结构发生变化的时候务必要调用确保滚动的效果正常*/
			},
			scrollTo(){
				this.scroll && this.scroll.scrollTo.apply(this.scroll,arguments)
			},
			scrollToElement() {
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)/*betterscroll设置滚动到
				指定位置*/
      },
			
		},
		watch:{/*监听data数据变换当变化是触发函数*/
			data(){
				setTimeout(()=>{
					this.refresh()
				},this.refreshDelay)
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus"></style>


