import storage from 'good-storage'//本地缓存lcalstorage 封装的一个库

const SEARCH_KEY = '__search__'
const SEARCH_MAX_LEN = 7

const PLAY_KEY = '__play__'
const PLAY_MAX_LEN = 200

const FAVORITE_KEY = '__favorite__'
const FAVORITE_MAX_LEN = 200

function insertArray(arr, val, compare, maxLen) {//插入数组的封装 存贮数组 值 比较函数 最大值
  const index = arr.findIndex(compare)//查找当前数组中 是否有 compare
  if (index === 0) {//当 当前 数据为第一个 时 就直接 输出
    return
  }
  if (index > 0) {//当不是 第一个时 删除当前数据
    arr.splice(index, 1)
  }
  arr.unshift(val)//插入 第一位
  if (maxLen && arr.length > maxLen) {//当当前数组 长度大于我们设置的 最大长度时
    arr.pop()//删除最后一个
  }
}

function deleteFromArray(arr, compare) {//删除 方法
  const index = arr.findIndex(compare)
  if (index > -1) {
    arr.splice(index, 1)
  }
}

export function saveSearch(query) {//本地缓存localstorage
  let searches = storage.get(SEARCH_KEY, [])//获取当前的搜索列表的数据
  insertArray(searches, query, (item) => {
    return item === query
  }, SEARCH_MAX_LEN)
  storage.set(SEARCH_KEY, searches)//本地缓存存入
  return searches
}

export function deleteSearch(query) {//删除本地缓存
  let searches = storage.get(SEARCH_KEY, [])
  deleteFromArray(searches, (item) => {
    return item === query
  })
  storage.set(SEARCH_KEY, searches)
  return searches
}

export function clearSearch() {
  storage.remove(SEARCH_KEY)
  return []
}

export function loadSearch() {//读取本地缓存数据
  return storage.get(SEARCH_KEY, [])
}

export function savePlay(song) {//本地缓存 播放过得歌曲
  let songs = storage.get(PLAY_KEY, [])
  insertArray(songs, song, (item) => {
    return song.id === item.id
  }, PLAY_MAX_LEN)
  storage.set(PLAY_KEY, songs)
  return songs
}

export function loadPlay() {
  return storage.get(PLAY_KEY, [])
}

export function saveFavorite(song) {//本地缓存 收藏列表
  let songs = storage.get(FAVORITE_KEY, [])
  insertArray(songs, song, (item) => {
    return song.id === item.id
  }, FAVORITE_MAX_LEN)
  storage.set(FAVORITE_KEY, songs)
  return songs
}

export function deleteFavorite(song) {//获取
  let songs = storage.get(FAVORITE_KEY, [])
  deleteFromArray(songs, (item) => {
    return item.id === song.id
  })
  storage.set(FAVORITE_KEY, songs)
  return songs
}

export function loadFavorite() {
  return storage.get(FAVORITE_KEY, [])
}




