<template>
  <div class="song-list">
    <ul>
      <li v-for='song,index in songs' 
          class="item"
          @click="selectItem(song,index)">
          <div class="rank" v-show="rank">
            <div :class="getRankClass(index)" v-html="getHtml(index)"></div>
          </div>
        <div class="content">
          <h2 class="name">{{song.name}}</h2>
          <p class="desc">{{getDesc(song)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script type='text/ecmascript-6'>
  export default{
    props:{
      songs:{
        type:Array,
        default:[]
      },
      rank:{
        type:Boolean,
        default:false
      }
    },
    methods:{
      getRankClass(index){
        if(index<=2){
          return `icon icon${index}`
        }else{
          return 'icon'
        }
      },
      selectItem(item,index){
        this.$emit('select',item,index)
      },
      getHtml(index){
        if(index>2){
          return index + 1
        }
      },
      getDesc(song) {
        return ''+song.singer+'.'+song.album+''
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .song-list
    .item
      padding-left: 20px
      display: flex
      align-items: center
      box-sizing: border-box
      height: 64px
      font-size: $font-size-medium
      .rank
        flex: 0 0 25px
        width: 25px
        margin-right: 30px
        text-align: center
        color: yellow
        .icon
          display: inline-block
          width: 25px
          height: 24px
          font-weight: bold
          background-size: 25px 24px
          &.icon0
            bg-image('first')
          &.icon1
            bg-image('second')
          &.icon2
            bg-image('third')
        .text
          color: $color-theme
          font-size: $font-size-large
      .content
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          no-wrap()
          color: $color-text
        .desc
          no-wrap()
          margin-top: 4px
          color: $color-text-d
</style>


