<template>
  <div class="progress-bar" ref="progressBar" @click='progressClick'>
    <div class="bar-inner">
      <div class="progress" ref='progress'></div>
      <div class="progress-btn-wrapper" ref="progressBtn">
        <div class="progress-btn"
            @touchstart.prevent="progressTouchStart"
            @touchmove.prevent="progressTouchMove"
            @touchend.prevent="progressTouchEnd"
        ></div>
      </div>
    </div>
  </div>
</template>
<script>
import {prefixStyle} from 'common/js/dom'
const progressBtnWidth = 16
const transform = prefixStyle('transform')
export default {
  
  props:{
    percent:{// 父级传入的播放进度
      type:Number,
      default: 0
    }
  },
  created(){
    this.touch = {}
  },
  methods:{
    progressClick(e){//点击时 获取当前的点击时的offsetX的值
    //  this.offset(e.offsetX)//设定点击时的 播放进度条位置
     //当我们点击到progressBtn 的时候e.offsetX 取值 不对 
     const rect = this.$refs.progressBar.getBoundingClientRect()
     const offsetWidth = e.pageX - rect.left
     this.offset(offsetWidth)
     this._triggePercent()//
    },
    progressTouchStart(e){//
     this.touch.initiated = true
     this.touch.startX = e.touches[0].pageX
     this.touch.left = this.$refs.progress.clientWidth
    },
    progressTouchMove(e){
      if(!this.touch.initiated){
        return
      }
      const deltx = e.touches[0].pageX-this.touch.startX
      const offsetWidth = Math.min(this.$refs.progressBar.clientWidth - progressBtnWidth,Math.max(0,deltx+this.touch.left))
      this. offset(offsetWidth)
    },
    progressTouchEnd(){
      this.touch.initiated = false
      this._triggePercent()
    },
    _triggePercent(){//计算出当前的  进度条的百分比 并传到父组件
      const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
      const percent = this.$refs.progress.clientWidth/barWidth
      this.$emit('percentChange',percent)
    },
    offset(offsetWidth){//根据闯入的offsetWidth值来设定播放进度条的黄色部分的值 和圆形按钮的位置
   this.$refs.progress.style.width = `${offsetWidth}px`
   this.$refs.progressBtn.style.transform = `translate3d(${offsetWidth}px,0,0)`     
    }
  },
  watch:{
    percent(newPercent){//检测 父级传入的播放进度 调整进度条
      if(newPercent>0&&!this.touch.initiated){
        const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
        const offsetWidth = newPercent*barWidth
        this.offset(offsetWidth)
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme
</style>




