<template>
 <div class="player" v-show="playList.length>0">
   <transition name="normal"
              @enter='enter'
              @after-enter='afterEnter'
              @leave = 'leave'
              @after-leave='afterLeave' 
   >
      <div class="normal-player" v-show="fullScreen">
        <div class="background">
          <img width="100%" height="100%" :src="currentSong.image">
        </div>
        <div class="top">
          <div class="back" @click="back">
            <i class="icon-back"></i>
          </div>
          <h1 class="title" v-html="currentSong.name"></h1>
          <h2 class="subtitle" v-html="currentSong.singer"></h2>
        </div>
        <div class="middle" 
            @touchstart.prevent='middleTouchStart'
            @touchmove.prevent='middleTouchMove'
            @touchend='middleTouchEnd'
            >
          <div class="middle-l" ref="middlel">
            <div class="cd-wrapper" ref="cdWrapper">
              <div class="cd" :class="cdCls">
                <img class="image" :src="currentSong.image">
              </div>
            </div> 
            <div class="playing-lyric-wrapper">
              <div class="playing-lyric">{{playingLyric}}</div>
            </div>
            <div class="playing-lyric-wrapper">
              <div class="playing-lyric"></div>
            </div>
          </div>
          <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
            <div class="lyric-wrapper">
              <div v-if="currentLyric">
                <p class="text"
                  ref="lyricLine"
                  :class="{'current' : currentLineNub == index}"
                  v-for="line,index in currentLyric.lines"
                >{{line.txt}}</p>
              </div>
            </div>
          </scroll>
        </div>
        <div class="bottom">
          <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow==='cd'}">></span>
            <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
          </div>
          <div class="progress-wrapper">
            <span class="time time-l">{{format(currentTime)}}</span>
            <div class="progress-bar-wrapper">
            <progress-bar :percent='perent' @percentChange='onProgressBarChange'></progress-bar
            </div>
            </div>
            <span class="time time-r">{{format(currentSong.duration)}}</span>
          </div>
          <div class="operators">
            <div class="icon i-left" @click = 'changeMode'>
              <i :class='iconMode'></i>
            </div>
            <div class="icon i-left" :class="disableCls">
              <i @click="prev" class="icon-prev"></i>
            </div>
            <div class="icon i-center">
              <i :class='playIcon' @click="togglePlaying"></i>
            </div>
            <div class="icon i-right" :class="disableCls">
              <i @click="next" class="icon-next"></i>
            </div>
            <div class="icon i-right">
              <i class="icon" @click="toggleFavorite(currentSong)" :class="getFavoriteIcon(currentSong)"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name='mini'>
      <div class="mini-player" v-show="!fullScreen" @click='open'>
        <div class="icon">
          <img :class="cdCls" width="40" height="40" :src='currentSong.image'>
        </div>
        <div class="text">
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc"></p>
        </div>
        <div class="control"> 
          <progress-cirtle>
          <i :class="miniIcon" class="icon-mini" @click.stop="togglePlaying"></i>       
          </progress-cirtle>
        </div>
        <div class="control" @click.stop="showPlayList">
          <i class="icon-playlist"></i>
        </div>
      </div>  
     </transition> 
     <playList ref="playlist"></playList>
    <audio ref="audio" :src="currentSong.url" @play="ready"
    @error="error" @timeupdate="updateTime" @ended='end'></audio>
  </div>
</template>
<script>
  import {mapGetters,mapMutations,mapActions} from 'vuex'//引入的是vuex 语法糖 mapGetters 获取数据  mapMutations 修改数据
  import Scroll from 'base/scroll/scroll'
  import animations from 'create-keyframe-animation' //animations 动画实现
  import {prefixStyle} from 'common/js/dom' // 修改样式
  import ProgressBar from 'base/progress-bar/progress-bar'// 播放进度条
  import ProgressCirtle from 'base/progress-cirtle/progress-cirter'// 小播放器的原型播放进度条
  import {playMode} from 'common/js/config' //播放模式的配置
  import {shuffle} from 'common/js/util'//数组打乱
  import Lyric from 'lyric-parser'
  import playList from 'components/playlist/playlist'
  import {playerMixin} from 'common/js/mixin'
  const transform = prefixStyle('transfrom')
  export default {
    mixins:[playerMixin],
    data(){
      return {
        songReady: false,//是否缓存完毕
        currentTime: 0,//歌曲总时间
        currentLyric: null,
        currentLineNub: 0,
        currentShow:'cd',
        opacity:0,
        playingLyric:''
      }
    },
    computed: {
      // iconMode(){//播放模式的改变
      //   return this.mode === playMode.sequence? 'icon-sequence' :this.mode === 
      //   playMode.loop ? 'icon-loop': 'icon-random'
      // },
      cdCls(){ //根据当前的播放状态 来改变播放背景图的 class 名字来实现播放时背景图片的旋转和通知
        return this.playing? 'play' : 'play pause'
      },
      playIcon(){//改变当前的背景图的样式 播放 还是 暂停
        return this.playing? 'icon-pause':'icon-play'
      },
      miniIcon(){//迷你播放器的 旋转
        return this.playing? 'icon-pause-mini':'icon-play-mini'
      },
      disableCls(){//当前的歌曲是否缓冲完毕 两个状态两种样式
        return this.songReady? '' : 'disable'
      },
      perent(){//当前的播放的时间和总时间的比值
        return this.currentTime/ this.currentSong.duration
      },
      ...mapGetters([//从vuex 的state 中获取的数据
        'fullScreen',
        'playList',
        'currentSong',
        'playing',
        'currentIndex',
        'mode',
        'sequenceList'
      ])
    },
    created(){
      this.touch = {}
    },
    methods:{
       ...mapActions([
        'savePlayHistory'
      ]),
      showPlayList(){//显示播放列表
        this.$refs.playlist.show()
      },
      middleTouchStart(e){
        //标志已经初始完毕
        console.log(1)
        this.touch.initiated = true
        const touch = e.touches[0]
        this.touch.startX = touch.pageX
        this.touch.startY = touch.pageY
      },
      middleTouchMove(e){
        if(!this.touch.initiated){
          return
        }
        
        const touch = e.touches[0]
        const deltaX = touch.pageX - this.touch.startX
        const deltaY = touch.pageY - this.touch.startY
        if(Math.abs(deltaY)>Math.abs(deltaX)){
          return
        }
       
        const left = this.currentShow === 'cd' ? 0:-window.innerWidth
        const offsetwidth =Math.min(Math.max(-window.innerWidth,left+deltaX))
        this.$refs.lyricList.$el.style.transform = `translate3d(${offsetwidth}px,0,0)`
        this.touch.percent = Math.abs(offsetwidth / window.innerWidth)
        this.$refs.lyricList.$el.style.transition = '0'
        this.$refs.middlel.style.opacity = 1-this.touch.percent
        this.$refs.middlel.style.transition = '0'
  },
      middleTouchEnd(e){
        let offsetwidth 
        let opacity 
        if(this.currentShow === 'cd'){
          if(this.touch.percent > 0.1){
            offsetwidth = -window.innerWidth
            opacity = 0
            this.currentShow = 'lyric'
          }else{
            offsetwidth = 0
          }
        }else{
          if(this.touch.percent<0.9){
            offsetwidth = 0
            this.currentShow = 'cd'
            opacity = 1
          }else{
            offsetwidth = -window.innerWidth
            opacity = 0
          }
        }
        this.$refs.lyricList.$el.style.transform = `translate3d(${offsetwidth}px,0,0)`
        this.$refs.lyricList.$el.style.transition = '300ms'
         this.$refs.middlel.style.opacity = opacity
        this.$refs.middlel.style.transition = '0'
        console.log(this.$refs.lyricList.$el.style,1)
     },
      getLyric(){
        this.currentSong.getLyric().then((lyric)=>{
          if(this.currentSong.lyric !== lyric){
            return
          }
          this.currentLyric = new Lyric(lyric,this.handleLuric)
          if(this.playing){
            this.currentLyric.play()
          }
        }).catch(()=>{//当获取歌词失败时 数据清0
          this.currentLyric = null
          this.playingLyric = ''
          this.currentLineNub = 0
        })
      },
      handleLuric({lineNum,txt}){//歌词滚动设置
        this.currentLineNub = lineNum
        if(lineNum>5){
          let lineEl = this.$refs.lyricLine[lineNum-5]
          this.$refs.lyricList.scrollToElement(lineEl,1000)
        }else{
          this.$refs.lyricList.scrollToElement(0,0,1000)

        }
        this.playingLyric = txt
      },
      end(){//当 当前的 歌曲播放完毕时
        if(this.mode === playMode.loop){ //判断当前的播放模式 若当前的模式为单曲循环的话
          this.loop()
        }else{
          this.next()
        }
      },
      loop(){//歌曲播放结束的时候 当当前播放模式为单曲循环的时候 就将 播放事件 设置为0 在重新播放
        this.$refs.audio.currentTime = 0
        this.$refs.audio.play()
        if(this.currentLyric){
          this.currentLyric.seek(0)//歌词从头开始
        }
      },
      // changeMode(){//修改模式
      //   console.log(this.mode)
      //   const mode = (this.mode+1)%3
      //   this.setPlayMode(mode)
      //   let list = null
      //   if(mode === playMode.random){
      //     list = shuffle(this.sequenceList)
      //   }else{
      //     list = this.sequenceList
      //   }
      //   this.resetCurrentIndex(list)
      //   this.setPlayList(list)
      // },
      resetCurrentIndex(list){//findIndex es6语法 可拿到每个list 元素
        let index = list.findIndex((item)=>{//输index 的值等于当前的播放元素的id值
          return item.id === this.currentSong.id
        })
        this.setCurrentIndex(index)//在重新设定setCurrentIndex(播放歌曲的id值)
      },
      onProgressBarChange(percent){//进度条改变的时候 percent 接收的是子组件播放进度条传入的 修改后的播放进度比值
        const currentTime = this.currentSong.duration * percent
        this.$refs.audio.currentTime = this.currentSong.duration*percent//修改播放事件
        if(!this.playing){//是指 如果当前是在暂停中的换就让他播放
          this.togglePlaying()
        }
        if(this.currentLyric){
          this.currentLyric.seek(currentTime*1000)//修改歌词播放时间 让歌词滚动到播放时间对应的位置
        }
      },
      back(){//修改vuex 的state 中存储的 fullscrenn的值
        this.setFullScreen(false)
      },
      open(){//同上
        this.setFullScreen(true)
      },
      enter(el,done){//动画效果
        const {x,y,scale} = this._getPosAndScale()

        let animation = {
          0:{
            transform:`translate3d(${x}px,${y}px,0) scale(${scale})`
          },
          60:{
            transform:`translate3d(0,0,0) scale(1.1)`
          },
          100:{
            transform:`translate3d(0,0,0) scale(1)`           
          }
        }

        animations.registerAnimation({
          name:'move',
          animation,
          presets:{
            duration: 400,
            easing:'linear'
          }
        })
        animations.runAnimation(this.$refs.cdWrapper,'move',done)
      },
      afterEnter(){
        animations.unregisterAnimation('move')
        this.$refs.cdWrapper.style.animation = ''
      },
      leave(el, done) {
        this.$refs.cdWrapper.style.transition = 'all 0.4s'
        const {x, y, scale} = this._getPosAndScale()
        this.$refs.cdWrapper.style.transform = `translate3d(${x}px,${y}px,0) scale(${scale})`
        this.$refs.cdWrapper.addEventListener('transitionend', done)
      },
      afterLeave() {
        this.$refs.cdWrapper.style.transition = ''
        this.$refs.cdWrapper.style.transform = ''
      },
      _getPosAndScale(){//歌曲播放页面缩小的位置
        const targetWidth = 40
        const paddingLeft = 40
        const paddingBottom = 30 
        const paddingTop = 80
        const width = window.innerWidth*0.8
        const scale = targetWidth/width
        const x = -(window.innerWidth/2-paddingLeft)
        const y = window.innerHeight - paddingTop - width/2 - paddingBottom
        return {
          x,
          y,
          scale
        }
      },
      togglePlaying(){//改变播放状态
        if(!this.songReady){
          return
        }
        this.setPlayingState(!this.playing)    
        if(this.currentLyric){
          this.currentLyric.togglePlay()
        }  
      },
      next(){//点击下一首的时候
        if(!this.songReady){//当当前歌曲缓存没有完成时 就直接return出去 
          return
        }     
        if(this.playList.length == 1){
          this.loop()
          return
        }else{
        let index = this.currentIndex + 1 //改变当前的播放歌曲
        if(index == this.playList.length){//边界处理 当当前 为播放目录的最后一个的时候
            index = 0
        }
        this.setCurrentIndex(index)//改变播放 歌曲
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady = false
        }
      },
      prev(){//上一曲 切换 同上
        if(!this.songReady){
          return
        }
        if(this.playList.length == 1){
          this.loop()
          return
        }else{
        let index = this.currentIndex -1
        if(index == -1){
            index = this.playList.length-1
        }
        this.setCurrentIndex(index)
         if(!this.playing){
          this.togglePlaying()
        }
        this.songReady = false
        }
      },
      ready(){//歌曲是否缓存完毕
        this.songReady = true
        this.savePlayHistory(this.currentSong)
      },
      error(){//当前错误状态
        this.songReady = true
      },
      updateTime(e){//当前音频播放时间
        this.currentTime = e.target.currentTime
      },
      format(interval){//对传入的数据进行处理 对当前播放时间
        interval = interval || 0
        const minute = interval/60 | 0
        const second = this._pad(interval % 60 | 0)
        return `${minute}:${second}`
      },
      _pad(num,n = 2){//补位 若 没有两位 前面补0
        let len = num.toString().length
        while(len < n){
          num = '0' + num
          len++ 
        }
        return num
      },
      ...mapMutations({
        setFullScreen: 'SET_FULL_SCREEN',
        setPlayingState: 'SET_PLAYING_STATE',
        setCurrentIndex: 'SET_CURRENT_INDEX',
        setPlayMode: 'SET_PLAY_MODE',
        setPlayList: 'SET_PLAYLIST',
      })
    },
    watch:{//时间监听
      currentSong(newsong,oldsong){//监听播放 歌曲的改变
        if(!newsong.id){
          return
        }
        if(newsong.id==oldsong.id){//若当前的 歌曲播放id 相同 就直接跳出 解决 播放进度条改变时的 歌曲重加载的问题
          return
        }
        if(this.currentLyric){
          this.currentLyric.stop()
        }
        clearTimeout(this.timer)
       this.timer = setTimeout(()=>{//$nextTick延迟回调 dom 修改时间 反应
         this.$refs.audio.play()
         this.getLyric()
        },1000)
      },
      playing(newPlaying){
        const audio = this.$refs.audio
        this.$nextTick(()=>{
        newPlaying?audio.play() : audio.pause()
        })
      }
    },
    components:{
      Scroll,
      ProgressBar,
      ProgressCirtle,
      playList
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: $color-background
      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        opacity: 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-text
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-text
      .middle
        position: fixed
        width: 100%
        top: 80px
        bottom: 170px
        white-space: nowrap
        font-size: 0
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 80%
          .cd-wrapper
            position: absolute
            left: 10%
            top: 0
            width: 80%
            height: 100%
            .cd
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 10px solid rgba(255, 255, 255, 0.1)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-text-l
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-text-l
              font-size: $font-size-medium
              &.current
                color: $color-text
      .bottom
        position: absolute
        bottom: 50px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 20px
              border-radius: 5px
              background: $color-text-ll
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 10px 0
          .time
            color: $color-text
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-theme-d
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-highlight-background
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-text
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini, .icon-playlist
          font-size: 30px
          color: $color-theme-d
        .icon-mini
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>


